// Presence plugin.
import { name, reducer } from '../interface'
import createAPI from '../interface/api'

// Subscription plugin.
import { registerService as registerServiceAction } from '../../subscription/interface/actions'

import createMiddleware from './middleware'
import createOperations from './operations'
import createRequests from './requests'
import { STATUS, ACTIVITY } from './constants'

/**
 * CPaaS Presence plugin factory.
 * @method cpaasPresence
 * @return {Object} A plugin.
 */
export default function presenceFactory (options = {}, bottle) {
  // Add plugin-specific things to the API.
  function augmentedApi (container) {
    const newApi = createAPI(container)

    /**
     * Possible presence status values.
     * @public
     * @static
     * @name statuses
     * @memberof presence
     * @type {Object}
     * @property {string} OPEN
     * @property {string} CLOSED
     * @example
     * const { statuses, activities } = client.presence
     * // Use the values when updating presence.
     * client.presence.update(statuses.OPEN, activities.AVAILABLE)
     */
    newApi.presence.statuses = STATUS
    /**
     * Possible presence activity values.
     * @public
     * @static
     * @name activities
     * @memberof presence
     * @type {Object}
     * @property {string} AVAILABLE
     * @property {string} BUSY
     * @property {string} LUNCH
     * @property {string} VACATION
     * @property {string} ON_THE_PHONE
     * @property {string} ACTIVITIES_UNKNOWN
     * @property {string} ACTIVITIES_OTHER
     * @example
     * const { statuses, activities } = client.presence
     * // Use the values when updating presence.
     * client.presence.update(statuses.OPEN, activities.AVAILABLE)
     */
    newApi.presence.activities = ACTIVITY
    return newApi
  }

  // Invoke it once to do the registration for 'presence' service
  function registerPresence (container) {
    const { context } = container
    const { dispatch } = context
    // This will add the 'presence' service as valid service under the
    // Redux state: state.subscription.registeredServices
    dispatch(registerServiceAction('presence'))
  }

  // Register the component factory functions to the bottle.
  bottle.factory('PresenceAPI', augmentedApi)
  bottle.factory('PresenceOperations', createOperations)
  bottle.factory('PresenceRequests', createRequests)

  const capabilities = ['presence']

  // Register a function to do the presence registration after SDK initialization has finished.
  bottle.defer(registerPresence)

  return {
    capabilities,
    name,
    reducer,
    middleware: createMiddleware
  }
}
