// Users' Actions
import * as actions from '../interface/actions/users'
import BasicError, { usersCodes } from '../../errors'
import { getIdentity, getRequestInfo } from '../../auth/interface/selectors'

// Constants
import { platforms } from '../../constants'

// Libraries
import { localContactFromRemote, localUserFromRemote, remoteContactFromLocal } from './utils'
import * as contactsActions from '../../../src/users/interface/actions/contacts'
import { isEmpty } from 'lodash/fp'

/**
 * Users Operations factory function for CPaaS.
 * @method createOperations
 * @param  {Object} container The bottle container.
 * @return {Object} Available operations for Users plugin.
 */
export default function createOperations (container) {
  const { context, logManager, UsersRequests: requests } = container
  const log = logManager.getLogger('USERS')

  /**
   * Operation to fetch information about a user.
   * @method clickToCall
   * @param {string} userId The User ID of the user.
   * @returns {Object} The user data associated with userId.
   * @throws {Error} An error if request fails.
   */
  async function fetchUser (userId) {
    const { dispatch } = context

    try {
      // Dispatch a fetch action for backwards-compatible of action logs.
      //     This does not affect state or emit an event.
      dispatch(actions.fetchUser(userId))

      const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

      const userData = await requests.directorySearch(requestInfo, { userId, sortBy: 'name', order: 'asc' })
      if (userData.directoryItem.length > 0) {
        if (userData.directoryItem.length > 1) {
          log.info('Fetch User request returned more than 1 user', userData.directoryItem)
        }
        const localUser = localUserFromRemote(userData.directoryItem[0])
        dispatch(actions.fetchUserFinish(localUser))
        return localUser
      }
    } catch (err) {
      // Map the error we received to a generic one
      const error = new BasicError({
        code: usersCodes.DIRECTORY_REQUEST_FAIL,
        message: 'Directory search request resulted in an error being returned from the server'
      })
      dispatch(actions.fetchUserFinish({ error }))

      // Rethrow the error so it makes it back to the API.
      throw error
    }
  }

  /**
   * Fetch the user information from the directory for the current user.
   * This API is simply a shortcut for the {@link user.fetch user.fetch(getUserInfo().identity)} API.
   *
   * @method fetchSelf
   * @returns {Object} Fetch request's response for current user, parsed.
   * @throws {Error} An error if request fails.
   */
  async function fetchSelf () {
    const { dispatch } = context

    try {
      // Dispatch a fetch action for backwards-compatible of action logs.
      //     This does not affect state or emit an event.
      dispatch(actions.fetchSelfInfo())

      const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)
      const identity = getIdentity(context.getState())

      const userData = await requests.directorySearch(requestInfo, {
        userName: identity.split('@')[0],
        sortBy: 'name',
        order: 'asc'
      })

      if (userData.directoryItem.length > 0) {
        // TODO: Investigate why do we ever get more than one user
        if (userData.directoryItem.length > 1) {
          log.info('Fetch Self Info returned more than 1 user', userData.directoryItem)
        }
        const localUser = localUserFromRemote(userData.directoryItem[0])
        dispatch(actions.fetchSelfInfoFinish(localUser))
        return localUser
      }
    } catch (err) {
      // TODO: Investigate wether we should also append `err.message` to message
      //       as original code neither used `code` nor `message` coming from err.
      const error = new BasicError({
        code: usersCodes.DIRECTORY_REQUEST_FAIL,
        message: 'Fetch Self Info request resulted in an error being returned from the server'
      })
      dispatch(actions.fetchSelfInfoFinish({ error }))

      // Rethrow the error so it makes it back to the API.
      throw error
    }
  }

  /**
   * Triggers a directory search using certain search criteria.
   *
   * @param {Object} filters The filters used for narrowing down the results
   * @param {Object} options The formatting parameters specifying how the result should be returned (e.g. sorted in ascending order, etc)
   * @returns {Object} A map of users that match the criteria specified by `filters`.
   * @throws {Error} An error if request fails.
   */
  async function searchDirectory (filters, options) {
    const { dispatch } = context

    try {
      // Dispatch a fetch action for backwards-compatible of action logs.
      //     This does not affect state or emit an event.
      dispatch(actions.searchDirectory(filters, options))

      const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

      const userData = await requests.directorySearch(requestInfo, { ...filters, ...options })

      const users = userData.directoryItem.map(localUserFromRemote)

      dispatch(actions.searchDirectoryFinish({ users }))
      return users
    } catch (err) {
      const error = new BasicError({
        code: usersCodes.DIRECTORY_REQUEST_FAIL,
        message: err.message
      })

      dispatch(actions.searchDirectoryFinish({ error }))

      // Rethrow the error so it makes it back to the API.
      throw error
    }
  }

  /**
   * Operation to add a contact to the address book.
   * @method addContact
   * @param {Object} contact the contact to be added.
   * @returns {Object} The created contact.
   * @throws {Error} An error if operation fails.
   */
  async function addContact (contact) {
    const { dispatch } = context

    // Dispatch addContact action for backwards-compatible of action logs.
    //     This does not affect state or emit an event.
    dispatch(contactsActions.addContact(contact))

    log.info('Attempting to add contact(s):', contact)

    // validate
    if (!contact || isEmpty(contact)) {
      log.info('Could not add contact(s); invalid input.')

      // TODO: check and throw the proper error code.
      const validationError = new BasicError({
        code: usersCodes.INVALID_PARAM,
        message: 'Could not add contact(s); invalid input.'
      })

      // Dispatch the failure action. This is for logging backwards compatibility.
      dispatch(contactsActions.addContactFinish({ error: validationError }))

      // Throw the error, so it makes it back to the API.
      throw validationError
    }

    try {
      const contactBody = remoteContactFromLocal(contact, true)
      const result = await requests.addContact({ body: JSON.stringify(contactBody) })

      if (result) {
        const localContact = localContactFromRemote(result.result)

        // Dispatch addContactFinish action for backwards-compatible of action logs.
        //     This does not affect state or emit an event.
        dispatch(contactsActions.addContactFinish({ contact: localContact }))

        return localContact
      }
    } catch (error) {
      // Dispatch the failure action. This is for action logs backwards compatibility.
      dispatch(contactsActions.addContactFinish({ error }))

      // Rethrow the error so it makes it back to the API.
      throw error
    }
  }

  /**
   * Operation to remove contact with specified id.
   * @method removeContact
   * @param {string} contactId The User ID of the user.
   * @returns {string} The id associated with removed contact.
   * @throws {Error} An error if request fails.
   */
  async function removeContact (contactId) {
    const { dispatch } = context

    // Dispatch removeContact action for backwards-compatible of action logs.
    //     This does not affect state or emit an event.
    dispatch(contactsActions.removeContact(contactId))

    log.info('Attempting to remove the contact: ', contactId)

    // validate
    if (!contactId) {
      log.info('Could not remove contact; invalid id.')

      // TODO: check and throw the proper error code.
      const validationError = new BasicError({
        code: usersCodes.INVALID_PARAM,
        message: 'Could not remove contact; invalid id.'
      })

      // Dispatch the failure action. This is for logging backwards compatibility.
      dispatch(contactsActions.removeContactFinish({ error: validationError }))

      // Throw the error, so it makes it back to the API.
      throw validationError
    }

    try {
      const result = await requests.removeContact(contactId)

      if (result) {
        // Dispatch removeContactFinish action for backwards-compatible of action logs.
        //     This does not affect state or emit an event.
        dispatch(contactsActions.removeContactFinish({ contactId }))

        return contactId
      }
    } catch (error) {
      // Dispatch the failure action. This is for action logs backwards compatibility.
      dispatch(contactsActions.removeContactFinish({ error }))

      // Rethrow the error so it makes it back to the API.
      throw error
    }
  }

  /**
   * Operation to fetch a contact from the personal address book of the subscribed user.
   * @method fetchContact
   * @param {string} contactId The User ID of the user.
   * @returns {Object} The contact associated with the provided contactId.
   * @throws {Error} An error if request fails.
   */
  async function fetchContact (contactId) {
    const { dispatch } = context

    // Dispatch fetchContact action for backwards-compatible of action logs.
    //     This does not affect state or emit an event.
    dispatch(contactsActions.fetchContact(contactId))

    log.info('Attempting to fetch the contact: ', contactId)

    // validate
    if (!contactId) {
      log.info('Could not fetch contact; invalid id.')

      // TODO: check and throw the proper error code.
      const validationError = new BasicError({
        code: usersCodes.INVALID_PARAM,
        message: 'Could not fetch contact; invalid id.'
      })

      // Dispatch the failure action. This is for logging backwards compatibility.
      dispatch(contactsActions.fetchContactFinish({ error: validationError }))

      // Throw the error, so it makes it back to the API.
      throw validationError
    }
    try {
      const result = await requests.fetchContact(contactId)

      if (result) {
        const localContact = localContactFromRemote(result.result)

        // Dispatch fetchContactFinish action for backwards-compatible of action logs.
        //     This does not affect state or emit an event.
        dispatch(contactsActions.fetchContactFinish({ contact: localContact }))

        return localContact
      }
    } catch (error) {
      // Dispatch the failure action. This is for action logs backwards compatibility.
      dispatch(contactsActions.fetchContactFinish({ error }))

      // Rethrow the error so it makes it back to the API.
      throw error
    }
  }

  /**
   * Operation to update a contact in the address book.
   * @method updateContact
   * @param {Object} contact The contact that needs to be updated.
   * @returns {Object} The contact that was updated, if this operation was successful.
   * @throws {Error} An error if this operation fails.
   */
  async function updateContact (contact) {
    const { dispatch } = context

    // Dispatch an updateContact action for backwards-compatible of action logs.
    //     This does not affect state or emit an event.
    dispatch(contactsActions.updateContact({ contact }))

    // validate
    if (!contact || !contact.contactId) {
      // contactId is a mandatory field
      log.info('Could not update contact; Missing property: contactId.')

      // TODO: check and throw the proper error code.
      const validationError = new BasicError({
        code: usersCodes.INVALID_PARAM,
        message: 'Missing property: contactId'
      })

      // Dispatch the failure action. This is for logging backwards compatibility.
      dispatch(contactsActions.updateContactFinish({ error: validationError }))

      // Throw the error, so it makes it back to the API.
      throw validationError
    }

    try {
      const contactBody = remoteContactFromLocal(contact, false)
      const contactData = await requests.updateContact({
        contactId: contact.contactId,
        body: JSON.stringify(contactBody)
      })

      // We'll always have a contactData with a results property. If not then an error is thrown.
      if (contactData) {
        const localContact = localContactFromRemote(contactData.result)
        dispatch(contactsActions.updateContactFinish({ contact: localContact }))

        return localContact
      }
    } catch (error) {
      dispatch(contactsActions.updateContactFinish({ error }))
      throw error
    }
  }

  /**
   * Operation to refresh the state's contacts by fetching the latest contacts from the user's address book.
   * @method refreshContacts
   * @returns {Object} The latest local contacts from the user's address book.
   * @throws {Error} An error if request fails.
   */
  async function refreshContacts () {
    const { dispatch } = context

    // Dispatch refreshContacts action for backwards-compatible of action logs.
    //     This does not affect state or emit an event.
    dispatch(contactsActions.refreshContacts())

    log.info('Attempting to refresh the contacts')

    try {
      const result = await requests.refreshContacts()

      if (result) {
        const localContacts = result.result.map(localContactFromRemote)

        // Dispatch refreshContactsFinish action for backwards-compatible of action logs.
        //     This does not affect state or emit an event.
        dispatch(contactsActions.refreshContactsFinish({ contacts: localContacts }))

        return localContacts
      }
    } catch (error) {
      // Dispatch the failure action. This is for action logs backwards compatibility.
      dispatch(contactsActions.refreshContactsFinish({ error }))

      // Rethrow the error so it makes it back to the API.
      throw error
    }
  }

  return {
    // API operations:
    fetchUser,
    fetchSelf,
    searchDirectory,
    addContact,
    removeContact,
    fetchContact,
    updateContact,
    refreshContacts
  }
}
