import * as actions from '../../subscription/interface/actions'
import * as presenceActions from '../interface/actions'
import * as eventTypes from '../interface/eventTypes'

import { getNotificationChannels } from '../../subscription/interface/selectors'

import BasicError from '../../errors'
import { presenceCodes, subscriptionCodes } from '../../errors/codes'

// Other plugins
import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'

// Selectors
import { getAllPresence } from '../interface/selectors'

// Constants
import { ACTIVITY, STATUS } from './constants'

/**
 * Presence Operations factory function.
 * @method createOperations
 * @param  {Object} container The bottle container.
 * @return {Object} Available operations for Presence.
 */
export default function createOperations (container) {
  const { context, emitEvent, logManager, PresenceRequests: requests } = container
  const log = logManager.getLogger('PRESENCE')

  /**
   * Map of old presence activity values to new values.
   * @type {Object}
   */
  const activityMapUcToCPaaS = {
    active: ACTIVITY.AVAILABLE,
    idle: ACTIVITY.BUSY,
    away: ACTIVITY.BUSY,
    lunch: ACTIVITY.LUNCH,
    other: ACTIVITY.ACTIVITIES_OTHER,
    busy: ACTIVITY.BUSY,
    vacation: ACTIVITY.VACATION,
    'on-the-phone': ACTIVITY.ON_THE_PHONE,
    unknown: ACTIVITY.ACTIVITIES_UNKNOWN
  }

  /**
   * Map of old presence status values to new values.
   * @type {Object}
   */
  const statusMapUcToCPaaS = {
    open: STATUS.OPEN,
    closed: STATUS.CLOSED
  }

  /**
   * Operation to subscribe for 'presence' service.
   * Creates a new presence subscription channel.
   * @method serviceSubscribe
   * @param {Object} serviceConfig   The subscription request config
   * @param {string} type     The type of notification channel for this subscription.
   * @throws {Error} An error if operation fails.
   */
  async function serviceSubscribe (serviceConfig, type) {
    const { dispatch } = context

    // We do some validation upfront for both parameters passed
    if (!serviceConfig || !serviceConfig.service || serviceConfig.service === '') {
      const error = new BasicError({
        message: 'Invalid parameter: serviceConfig',
        code: presenceCodes.INVALID_PARAM
      })
      dispatch(
        actions.reportSubscriptionFinished({
          service: 'presence',
          type,
          error
        })
      )
      throw error
    }

    if (!type || type === '') {
      const error = new BasicError({
        message: 'Invalid parameter: type',
        code: presenceCodes.INVALID_PARAM
      })
      dispatch(
        actions.reportSubscriptionFinished({
          service: serviceConfig.service,
          type,
          error
        })
      )
      throw error
    }

    const channels = getNotificationChannels(context.getState())
    const callbackURL = channels.notificationChannels.websocket.callbackURL

    log.debug('Subscribing to presence service')

    let response
    try {
      response = await requests.getPresenceLists()
    } catch (error) {
      dispatch(
        actions.reportSubscriptionFinished({
          service: serviceConfig.service,
          type,
          error
        })
      )
      throw error
    }
    log.debug('Received presenceLists response:', response)

    // check if there is a presence list already
    let presenceListId, newListResponse

    // check if there is an existing presenceList
    if (response.presenceList.length > 0) {
      const url = response.presenceList[0].resourceURL
      if (url) {
        const lastIndex = url.lastIndexOf('/')
        presenceListId = url.slice(lastIndex + 1)
      }
    } else {
      try {
        //  create a new presenceList
        newListResponse = await requests.createList([])
      } catch (error) {
        dispatch(
          actions.reportSubscriptionFinished({
            service: serviceConfig.service,
            type,
            error
          })
        )
        throw error
      }
      log.debug('Received newListResponse:', newListResponse)
      const resourceURL = newListResponse.resourceURL
      if (resourceURL) {
        presenceListId = resourceURL.substring(resourceURL.lastIndexOf('/') + 1)
      }
    }
    try {
      const subscribeResponse = await requests.subscribe(presenceListId, callbackURL)
      log.debug('Got subscribe response: ', subscribeResponse)

      dispatch(
        actions.reportSubscriptionFinished({
          service: serviceConfig.service,
          type,
          subscription: subscribeResponse.presenceListSubscription,
          error: undefined
        })
      )
    } catch (error) {
      dispatch(
        actions.reportSubscriptionFinished({
          service: serviceConfig.service,
          type,
          error
        })
      )
      throw error
    }
  }

  /**
   * Operation to unsubscribe for 'presence' service.
   * Deletes an existing presence subscription.
   * @method serviceUnsubscribe
   * @param {string} type     The type of notification channel for this subscription.
   * @throws {Error} An error if operation fails.
   */
  async function serviceUnsubscribe (type) {
    const { dispatch } = context
    if (!type || type === '') {
      const error = new BasicError({
        message: 'Invalid parameter: type',
        code: presenceCodes.INVALID_PARAM
      })
      dispatch(
        actions.reportUnsubscriptionFinished({
          service: 'presence',
          type,
          error
        })
      )
      throw error
    }

    const { subscriptions } = getNotificationChannels(context.getState())
    let presenceSubscription = subscriptions.filter(sub => {
      return sub.service === 'presence' && sub.channelType === type
    })

    let error
    if (presenceSubscription.length === 0) {
      // "Error" scenario: Can't unsubscribe to a service that we don't have.
      error = new BasicError({
        message: "Cannot unsubscribe to a service that we don't have",
        code: presenceCodes.INVALID_REQUEST
      })
      dispatch(
        actions.reportUnsubscriptionFinished({
          service: 'presence',
          type,
          error
        })
      )
      throw error
    } else if (presenceSubscription.length > 1) {
      // "Error" scenario: There shouldn't be able to have
      //    more than 1 of the same service.
      error = new BasicError({
        message: "There shouldn't be more than 1 of the same service",
        code: presenceCodes.INVALID_REQUEST
      })
      dispatch(
        actions.reportUnsubscriptionFinished({
          service: 'presence',
          type,
          error
        })
      )
      throw error
    } else {
      presenceSubscription = presenceSubscription[0]
    }
    const presenceListId = presenceSubscription.presenceListId
    const subscription = presenceSubscription.subscriptionId
    const subscriptionId = subscription.substring(subscription.lastIndexOf('/') + 1)

    log.info(`Unsubscribing from presence service on ${type} channel.`)
    try {
      const unsubscribeResponse = await requests.unsubscribe(presenceListId, subscriptionId)
      log.debug('Got unsubscribe response: ', unsubscribeResponse)

      dispatch(
        actions.reportUnsubscriptionFinished({
          ...unsubscribeResponse,
          service: 'presence',
          type
        })
      )
    } catch (error) {
      dispatch(
        actions.reportUnsubscriptionFinished({
          service: 'presence',
          type,
          error
        })
      )
      throw error
    }
  }

  /**
   * Operation to subscribe for 'presence' service.
   * @method presenceSubscribe
   * @param  {string} userId userId
   * @throws {Error} An error if operation fails.
   */
  async function subscribe (userId) {
    const { dispatch, getState } = context

    log.info(`Subscribing to the user(s) ${userId} presence.`)

    // Dispatch an updatePresence action for backwards-compatible of action logs.
    //     This does not affect state or emit an event.
    dispatch(presenceActions.subscribePresence(userId))

    // Check if no users were passed in
    if (!userId) {
      // No users passed in
      // TODO: confirm the proper error code for invalid userId.
      throw new BasicError({
        code: presenceCodes.INVALID_PARAM,
        message: 'No users provided to subscribe to.'
      })
    }

    // get current presenceListId
    const subscriptions = getNotificationChannels(getState())
    const presenceSubscription = subscriptions.subscriptions.filter(function (sub) {
      return sub.service === 'presence'
    })

    if (presenceSubscription.length === 0) {
      const error = new BasicError({
        code: subscriptionCodes.CPAAS_SERVICE_SUB_FAIL,
        message: 'Found no subscription for presence service. Subscribe for presence first and then try again.'
      })
      error.presentityUserId = [userId]

      // Dispatch an error action for backwards-compatible of action logs.
      //     This does not affect state or emit an event.
      dispatch(presenceActions.subscribePresenceFinish(error))

      // Throw the error, so it makes it back to the API.
      throw error
    } else {
      try {
        const presenceListId = presenceSubscription[0].presenceListId
        const response = await requests.addUser(userId, presenceListId)

        log.debug('Received response from addUser request:', response)

        // Dispatch an action for backwards-compatible of action logs.
        //     This does not affect state or emit an event.
        dispatch(presenceActions.subscribePresenceFinish(response))
      } catch (error) {
        log.info(`Failed to subscribe to users(s): ${userId}. ${error}`)
        error.presentityUserId = [userId]

        // Dispatch an error action for backwards-compatible of action logs.
        //     This does not affect state or emit an event.
        dispatch(presenceActions.subscribePresenceFinish(error))

        // Throw the error, so it makes it back to the API.
        throw error
      }
    }
  }

  /**
   * Operation to unsubscribe from 'presence' service.
   * @method presenceUnsubscribe
   * @param  {string} userId userId
   * @throws {Error} An error if operation fails.
   */
  async function unsubscribe (userId) {
    const { dispatch, getState } = context

    log.info(`Unsubscribing to the user(s) ${userId} presence.`)

    // Dispatch an updatePresence action for backwards-compatible of action logs.
    //     This does not affect state or emit an event.
    dispatch(presenceActions.unsubscribePresence(userId))

    // Check if no users were passed in
    if (!userId) {
      // No users passed in
      // TODO: confirm the proper error code for invalid userId.
      throw new BasicError({
        code: presenceCodes.INVALID_PARAM,
        message: 'No users provided to subscribe to.'
      })
    }

    // get current presenceListId
    const subscriptions = getNotificationChannels(getState())

    const presenceSubscription = subscriptions.subscriptions.filter(function (sub) {
      return sub.service === 'presence'
    })

    if (presenceSubscription.length === 0) {
      const error = new BasicError({
        message: 'Found no subscription for presence service.',
        code: subscriptionCodes.CPAAS_SERVICE_UNSUB_FAIL
      })
      error.presentityUserId = [userId]

      // Dispatch an error action for backwards-compatible of action logs.
      //     This does not affect state or emit an event.
      dispatch(presenceActions.unsubscribePresenceFinish(error))

      // Throw the error, so it makes it back to the API.
      throw error
    } else {
      try {
        const presenceListId = presenceSubscription[0].presenceListId

        const response = await requests.removeUser(userId, presenceListId)

        log.debug('Received response from removeUser request:', response)

        dispatch(presenceActions.unsubscribePresenceFinish(response))
      } catch (error) {
        log.info(`Failed to unsubscribe to users(s): ${userId}. ${error}`)

        error.presentityUserId = [userId]

        // Dispatch an error action for backwards-compatible of action logs.
        //     This does not affect state or emit an event.
        dispatch(presenceActions.unsubscribePresenceFinish(error))

        // Throw the error, so it makes it back to the API.
        throw error
      }
    }
  }

  /**
   * Operation to receive Presence info from a notification.
   * @method presenceReceived
   * @param {Object} action The action for receiving a user presence notification.
   */
  function presenceReceived (action) {
    const { dispatch } = context

    if (![NOTIFICATION_RECEIVED].includes(action.type)) {
      return
    }
    const notification = action.payload.presenceNotification
    const users = getAllPresence(context.getState())
    log.info(`Received notification of the user(s): ${notification.presentityUserId} present presence.`)

    if (
      notification.presence &&
      users[notification.presentityUserId] &&
      !users[notification.presentityUserId].isPending
    ) {
      const presence = {}
      presence.userId = notification.presentityUserId
      presence.activity = notification.presence.person.activities.activityValue
      presence.status = notification.presence.person['overriding-willingness'].overridingWillingnessValue
      presence.note = notification.presence.person.activities.other

      // For backwards compatibility
      dispatch(presenceActions.presenceReceived(presence))

      // Tell the application that state has changed.
      emitEvent(eventTypes.RECEIVED, presence)
    }
  }

  /**
   * CPaaS operation for updating user(s) presence.
   * @method update
   * @param  {string} status The status of the presence state.
   * @param  {string} activity The activity to be shown as presence state
   * @param  {string} [note] An additional note to be provided when the activity is `presence.activities.ACTIVITIES_OTHER`.
   * @return {Object} The updated presence.
   * @throws {Error} An error if operation fails.
   */
  async function update (status, activity, note) {
    const { dispatch } = context

    // Trigger an action for backwards compatibility
    dispatch(presenceActions.updatePresence(status, activity, note))

    // Convert the status to the CPaaS value (if required)
    if (status in statusMapUcToCPaaS) {
      status = statusMapUcToCPaaS[status]
    }

    // Convert the activity to the CPaaS value (if required)
    if (activity in activityMapUcToCPaaS) {
      activity = activityMapUcToCPaaS[activity]
    }

    let error

    // Verify that the status value is a valid CPaaS values
    if (Object.values(STATUS).indexOf(status) === -1) {
      log.info('Could not update presence: Invalid status.')
      error = new BasicError({
        code: presenceCodes.INVALID_STATUS,
        message: 'Invalid status in presence update request.'
      })
      dispatch(presenceActions.updatePresenceFinish(error))
      throw error
    }

    // Verify that the activity value is a valid CPaaS value
    if (Object.values(ACTIVITY).indexOf(activity) === -1) {
      log.info('Could not update presence: Invalid activity.')
      error = new BasicError({
        code: presenceCodes.INVALID_ACTIVITY,
        message: 'Invalid activity in presence update request.'
      })
      dispatch(presenceActions.updatePresenceFinish(error))
      throw error
    }

    try {
      const response = await requests.publish(status, activity, note)
      log.debug('Received response from publish request:', response)

      const presenceValues = {}
      presenceValues.status = response.presence.person['overriding-willingness'].overridingWillingnessValue
      presenceValues.activity = response.presence.person.activities.activityValue
      presenceValues.note = response.presence.person.activities.other

      dispatch(presenceActions.updatePresenceFinish(presenceValues))
      return presenceValues
    } catch (error) {
      log.debug(`Failed to update presence. Error is: ${error}`)
      // Dispatch an error action for backwards-compatible of action logs.
      //     This does not affect state or emit an event.
      dispatch(presenceActions.updatePresenceFinish(error))

      throw error
    }
  }

  /**
   * CPaaS operation for getting the presence updates one time only for the user(s).
   * @method fetch
   * @param  {Array<string>} users An array of User IDs.
   * @return {Object} The returned presence for the requested user(s).
   * @throws {Error} An error if operation fails.
   */
  async function fetch (users) {
    const { dispatch } = context

    // Trigger action for backward compatibility
    dispatch(presenceActions.getPresence(users))

    if (!users || users.length === 0) {
      log.info('Could not user(s); invalid input.')
      // TODO: check and throw the proper error code.
      const validationError = new BasicError({
        code: presenceCodes.INVALID_PARAM,
        message: 'Could not fetch user(s); invalid input.'
      })
      dispatch(presenceActions.getPresenceFinish({ error: validationError }))
      throw validationError
    }

    try {
      const response = await requests.retrievePresence(users)
      log.debug('Received response from retrievePresence request:', response)

      const presence = {}
      const presenceContact = response.presenceContact[0]
      presence.userId = presenceContact.presentityUserId
      if (presenceContact.presence) {
        presence.activity = presenceContact.presence.person.activities.activityValue
        presence.status = presenceContact.presence.person['overriding-willingness'].overridingWillingnessValue
        presence.note = presenceContact.presence.person.activities.other
      }
      dispatch(presenceActions.getPresenceFinish(presence))
      return presence
    } catch (error) {
      // Dispatch an error action for backwards-compatible of action logs.
      //     This does not affect state or emit an event.
      dispatch(presenceActions.getPresenceFinish(error))

      throw error
    }
  }

  return {
    serviceSubscribe,
    serviceUnsubscribe,
    presenceReceived,
    update,
    fetch,
    subscribe,
    unsubscribe
  }
}
