// Helpers
import { handleRequestError } from '../../common/helpers'

// Selectors
import { getRequestInfo } from '../../auth/interface/selectors'

// Helpers
import { platforms } from '../../constants'

// Presence plugin.
import { ACTIVITY } from './constants'
import BasicError, { presenceCodes } from '../../../src/errors'

/**
 * Presence CPaaS REST Requests factory function.
 * Defines the REST interface between the SDK and CPaaS's Presence REST APIs.
 * @method createRequests
 * @param  {Object} container The bottle container.
 * @return {Object} Available requests for Presence.
 */
export default function createRequests (container) {
  const { logManager, sendRequest, context } = container
  const log = logManager.getLogger('PRESENCE')

  /**
   * REST request to retrieve all presence lists.
   * @method getPresenceLists
   * @return {Object} The backend response containing the presence lists.
   */
  async function getPresenceLists () {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)
    const options = {
      method: 'GET',
      url: `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` + `${requestInfo.username}/presenceLists`
    }

    const response = await sendRequest(options)
    log.debug('Got response for presenceList request as: ', response)
    const { body, error } = response

    if (error) {
      throw handleRequestError(response, 'Get presence lists')
    } else {
      return body.presenceListCollection
    }
  }

  /**
   * CPaaS REST request to create a new presence list.
   * @method createList
   * @param  {string} user User ID of presentity
   * @return {Object}
   */
  async function createList (user) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    const requestBody = {
      presenceList: {
        'x-listName': 'myList',
        presenceContact: user
      }
    }

    const options = {
      method: 'POST',
      url: `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/${requestInfo.username}/presenceLists`,
      body: JSON.stringify(requestBody)
    }
    const response = await sendRequest(options)
    log.debug('Got response for createList request as: ', response)
    const { body, error } = response

    if (error) {
      throw handleRequestError(response, 'Create presence list')
    } else {
      return body.presenceList
    }
  }

  /**
   * CPaaS REST request to create a presence list subscription.
   * @method subscribe
   * @param  {string} presenceListId Resource ID of the presenceList
   * @param  {string} callbackURL Callback URL of websocket channel
   * @return {Object}
   */
  async function subscribe (presenceListId, callbackURL) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)
    const presenceListSubscription = {
      presenceListSubscription: {
        callbackReference: {
          notifyURL: callbackURL
        },
        clientCorrelator: requestInfo.clientCorrelator,
        duration: 86400
      }
    }

    const options = {
      method: 'POST',
      url:
        `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
        `${requestInfo.username}/subscriptions/presenceListSubscriptions/` +
        `${presenceListId}`,
      body: JSON.stringify(presenceListSubscription)
    }

    const response = await sendRequest(options)
    log.debug('Got response for subscribe request as: ', response)
    const { body, error } = response

    if (error) {
      throw handleRequestError(response, 'Subscribe to presence list')
    } else {
      return body
    }
  }

  /**
   * REST request to delete a subscription against a presence list.
   * @method unsubscribe
   * @param  {string} presenceListId Resource ID of the presenceList
   * @param  {string} subscriptionId Resource ID of the subscription
   * @return {Object}
   */
  async function unsubscribe (presenceListId, subscriptionId) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)
    const options = {
      method: 'DELETE',
      responseType: 'none',
      url:
        `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
        `${requestInfo.username}/subscriptions/presenceListSubscriptions/` +
        `${presenceListId}/${subscriptionId}`
    }

    const response = await sendRequest(options)
    log.debug('Got response for unsubscribe request as: ', response)
    const { body, error, result } = response

    if (error && result.code !== 404) {
      throw handleRequestError(response, 'Unsubscribe to presence list')
    } else {
      return body
    }
  }

  /**
   * REST request to create a new presence source with initial presence state.
   * @method publish
   * @param  {Object} userPresence
   * @param  {string} userPresence.status - user presence status
   * @param  {string} userPresence.activity - user presence activity
   * @param  {string} userPresence.note - user presence note
   * @return {Object}
   */
  async function publish (status, activity, note) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    const requestBody = {
      presenceSource: {
        clientCorrelator: requestInfo.clientCorrelator,
        presence: {
          person: {
            'overriding-willingness': {
              overridingWillingnessValue: status
            },
            activities: {
              activityValue: activity
            }
          }
        }
      }
    }

    if (note && activity === ACTIVITY.ACTIVITIES_OTHER) {
      requestBody.presenceSource.presence.person.activities.other = note
    }
    const options = {
      method: 'POST',
      url: `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` + `${requestInfo.username}/presenceSources`,
      body: JSON.stringify(requestBody)
    }

    const response = await sendRequest(options)
    const { body, error, result } = response

    if (error) {
      const anError = handleRequestError(response, 'Presence publish')

      // Handle errors from the request helper.
      const { message } = result
      const errMsg = `Update presence request failed: ${message}`
      log.info(errMsg)

      throw anError
    }
    return body.presenceSource
  }

  /**
   * REST request to retrieve presence states of requested presentities.
   * @method retrievePresence
   * @param  {Array} users Array of userId's.
   * @return {Object}
   */
  async function retrievePresence (users) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)
    const adhocPresenceList = {
      adhocPresenceList: {
        presentityUserId: users
      }
    }

    const options = {
      method: 'POST',
      url:
        `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` + `${requestInfo.username}/adhocPresenceList`,
      body: JSON.stringify(adhocPresenceList)
    }

    const response = await sendRequest(options)
    const { body, error, result } = response

    if (error) {
      const anError = handleRequestError(response, 'Fetch users presence')

      // Handle errors from the request helper.
      const { message } = result
      const errMsg = `Fetch users presence request failed: ${message}`
      log.info(errMsg)

      throw anError
    }

    return body.presenceList
  }

  /**
   * REST request to add a new presentity to the presence list.
   * @method addUser
   * @param  {string} userId userId
   * @param  {string} presenceListId Resource ID of the presenceList
   * @return {Object}
   */
  async function addUser (userId, presenceListId) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    // Credentials not set. Therefore, request is not sent (unauthorized issue).
    if (!requestInfo.username) {
      const message = 'Failed to add presence user; user not authenticated (credentials not set).'
      log.info(message)

      // TODO: confirm the proper error code.
      throw new BasicError({
        code: presenceCodes.INVALID_REQUEST,
        message
      })
    }

    const presenceContact = {
      presenceContact: {
        presentityUserId: userId
      }
    }

    const options = {
      method: 'PUT',
      url:
        `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
        `${requestInfo.username}/presenceLists/${presenceListId}/` +
        `presenceContacts/${userId}`,
      body: JSON.stringify(presenceContact)
    }

    const response = await sendRequest(options)

    const { body, error } = response

    if (error) {
      // Handle errors from the server.
      throw handleRequestError(response, 'Add presence user')
    } else {
      // success
      return body.presenceContact
    }
  }

  /**
   * REST request to delete a presentity from a presence list.
   * @method removeUser
   * @param  {string} userId userId
   * @param  {string} presenceListId Resource ID of the presenceList
   * @return {Object}
   */
  async function removeUser (userId, presenceListId) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    // Credentials not set. Therefore, request is not sent (unauthorized issue).
    if (!requestInfo.username) {
      const message = 'Failed to remove presence user; user not authenticated (credentials not set).'
      log.info(message)

      // TODO: confirm the proper error code.
      throw new BasicError({
        code: presenceCodes.INVALID_REQUEST,
        message
      })
    }

    const options = {
      method: 'DELETE',
      responseType: 'none',
      url:
        `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
        `${requestInfo.username}/presenceLists/${presenceListId}/` +
        `presenceContacts/${userId}`
    }

    const response = await sendRequest(options)

    const { error, result } = response

    if (error) {
      // Handle errors from the server.
      throw handleRequestError(response, 'Remove presence user')
    } else {
      // success
      return result
    }
  }

  return {
    getPresenceLists,
    createList,
    subscribe,
    unsubscribe,
    publish,
    retrievePresence,
    addUser,
    removeUser
  }
}
