// Helpers
import { handleRequestError } from '../../common/helpers/handleRequestError'

// Libraries
import { mapKeys, pick } from 'lodash/fp'
import BasicError, { usersCodes } from '../../../src/errors'
import { getRequestInfo } from '../../../src/auth/interface/selectors'
import { platforms } from '../../../src/constants'

/**
 * Users CPaaS REST Requests factory function.
 * Defines the REST interface between the SDK and CPaaS's Users REST APIs.
 * @method createRequests
 * @param  {Object} container The bottle container.
 * @return {Object} Available requests for Users.
 */
export default function createRequests (container) {
  const { context, logManager, sendRequest } = container
  const log = logManager.getLogger('USERS')

  /**
   * CPaaS REST request to trigger a directory search.
   * @method directorySearch
   * @param {Object} requestInfo
   * @param {Object} params
   * @param {string} params.callId A unique id representing the call.
   * @param {string} [params.sortBy] The name of the field to sort by.
   * @param {string} [params.order] The sorting order indicator. Possible values are `asc` or `desc`.
   */
  async function directorySearch (requestInfo, params = {}) {
    const directoryId = params.directoryId || 'default'
    const method = 'GET'
    const url = `${requestInfo.baseURL}/cpaas/directory/v1/${requestInfo.username}/${directoryId}/search`
    const queryParams = mapKeys(mapSearchKey, pick(validKeys, params))

    const response = await sendRequest({ url, queryParams, method })
    const { error, body } = response
    if (error) {
      // Handle the error similarly regardless of whether or not the response has a body value
      const error = handleRequestError(response, 'User Request')
      log.debug('Error performing directorySearch request', error)
      throw error
    } else if (body) {
      // Request was successful
      return body.directoryItemList
    } else {
      // Request was successful but no body was provided
      return { result: true }
    }
  }

  /**
   * CPaaS REST request to trigger add contact.
   *
   * Perform an HTTP request to create one contact resource.
   * @method addContact
   * @param {Object} params
   * @param {Object} params.body The contact to be added.
   * @returns {Object} The created contact.
   * @throws {Error} An error if request fails.
   */
  async function addContact (params = {}) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    const options = {
      url: `${requestInfo.baseURL}/cpaas/addressbook/v1/${requestInfo.username}/default/contacts`,
      body: params.body,
      method: 'POST',
      headers: {}
    }

    // Credentials not set. Therefore, request is not sent (unauthorized issue).
    if (!requestInfo.username) {
      const message = 'Failed to add contact; user not authenticated (credentials not set).'
      log.info(message)
      throw new BasicError({
        code: usersCodes.REFRESH_CONTACTS_FAIL,
        message
      })
    }

    const response = await sendRequest(options)

    const { body, error, result } = response

    if (error) {
      // Handle errors from the server.
      throw handleRequestError(response, 'Add Contact')
    } else if (body) {
      // Success
      log.info('Contact(s) created successfully')
      return { result: body.contact }
    } else {
      // No error, handle responses without a body status code. Either REST request issues or unknown server issues.
      return { result }
    }
  }

  /**
   * CPaaS REST request to trigger remove contact.
   *
   * Send an HTTP DELETE request to delete one contact.
   * @method removeContact
   * @param {string} contactId The uniquely identifier for the contact.
   * @returns {boolean} True if the contact is successfully removed.
   * @throws {Error} An error if request fails.
   */
  async function removeContact (contactId) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    const options = {
      url: `${requestInfo.baseURL}/cpaas/addressbook/v1/${requestInfo.username}/default/contacts/${contactId}`,
      method: 'DELETE',
      responseType: 'none'
    }

    // Credentials not set. Therefore, request is not sent (unauthorized issue).
    if (!requestInfo.username) {
      const message = 'Failed to remove contact; user not authenticated (credentials not set).'
      log.info(message)
      throw new BasicError({
        code: usersCodes.REFRESH_CONTACTS_FAIL,
        message
      })
    }

    const response = await sendRequest(options)

    if (response.error) {
      // Handle errors from the server.
      throw handleRequestError(response, 'Delete Contact Request')
    } else {
      // Successful DELETE requests sent to this endpoint do not return a body, thus we will return a boolean
      log.info('Contact removed successfully')
      return { result: true }
    }
  }

  /**
   * CPaaS REST request to trigger fetch contact
   *
   * Perform an HTTP request to retrieve one contact resource
   * @method fetchContact
   * @param {string} [contactId] The unique identifier of the contact
   * @returns {boolean} The fetched contact.
   * @throws {Error} An error if request fails.
   */
  async function fetchContact (contactId) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    const options = {
      url: `${requestInfo.baseURL}/cpaas/addressbook/v1/${requestInfo.username}/default/contacts/${contactId}`,
      method: 'GET'
    }

    // Credentials not set. Therefore, request is not sent (unauthorized issue).
    if (!requestInfo.username) {
      const message = 'Failed to fetch contact; user not authenticated (credentials not set).'
      log.info(message)
      throw new BasicError({
        code: usersCodes.REFRESH_CONTACTS_FAIL,
        message
      })
    }

    const response = await sendRequest(options)
    const { body, error } = response

    if (error) {
      // Handle errors from the server.
      throw handleRequestError(response, 'Fetch Contact')
    } else if (body) {
      // Success
      log.info('Contact(s) fetched successfully')
      return { result: body.contact }
    } else {
      // No error, but response does not contain a body
      return { result: true }
    }
  }

  /**
   * CPaaS REST request to trigger a contact update.
   *
   * Perform an HTTP request to update one contact resource.
   * @method updateContact
   * @param {Object} params
   * @param {string} params.contactId The unique id of the contact to be updated.
   * @param {string} params.body The request body for a PUT request which contains all the contact's fields that need to be updated.
   * @returns {boolean} The update contact, if the contact is successfully updated.
   * @throws {Error} An error if request fails.
   */
  async function updateContact (params = {}) {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    const options = {
      url: `${requestInfo.baseURL}/cpaas/addressbook/v1/${requestInfo.username}/default/contacts/${params.contactId}`,
      method: 'PUT',
      body: params.body,
      headers: {}
    }

    // Credentials not set. Therefore, request is not sent (unauthorized issue).
    if (!requestInfo.username) {
      const message = 'Failed to update contact; user not authenticated (credentials not set).'
      log.info(message)
      throw new BasicError({ code: usersCodes.REFRESH_CONTACTS_FAIL, message })
    }

    const response = await sendRequest(options)
    const { error, body, result } = response
    if (error) {
      throw handleRequestError(response, 'Update Contact')
    } else if (body) {
      return { result: body.contact }
    } else {
      // No error, but no body in the response
      return { result }
    }
  }

  /**
   * CPaaS REST request to trigger refresh contacts.
   *
   * Perform an HTTP request to retrieve all of a user's address book contacts.
   * @method refreshContacts
   * @returns {Array} A list of the latest contacts from the user's address book.
   * @throws {Error} An error if request fails.
   */
  async function refreshContacts () {
    const requestInfo = getRequestInfo(context.getState(), platforms.CPAAS)

    const options = {
      url: `${requestInfo.baseURL}/cpaas/addressbook/v1/${requestInfo.username}/default/contacts`,
      method: 'GET'
    }

    // Credentials not set. Therefore, request is not sent (unauthorized issue).
    if (!requestInfo.username) {
      const message = 'Failed to refresh contacts; user not authenticated (credentials not set).'
      log.info(message)
      throw new BasicError({
        code: usersCodes.REFRESH_CONTACTS_FAIL,
        message
      })
    }

    const response = await sendRequest(options)

    const { body, error } = response

    if (error) {
      // Handle errors from the server.
      throw handleRequestError(response, 'Refresh Contact')
    } else if (body) {
      // Success
      log.info('Contact(s) refreshed successfully')
      return { result: body.contactCollection.contact }
    } else {
      // No error, handle responses without a body status code. Either REST request issues or unknown server issues.
      throw new BasicError({
        message: 'Request succeeded but no body was provided in the response',
        code: usersCodes.UNKNOWN
      })
    }
  }

  return {
    // Operation requests.
    directorySearch,
    addContact,
    removeContact,
    fetchContact,
    updateContact,
    refreshContacts
  }
}

/**
 * valid directory search query keys
 *
 * @type {Array}
 */
const validKeys = [
  'name',
  'firstName',
  'lastName',
  'userId',
  'userName',
  'phoneNumber',
  'order',
  'sortBy',
  'max',
  'next'
]

/**
 * Helper function to return a validated parameter for directory searches
 * @param {string} key
 * @returns {string}
 */
function mapSearchKey (key) {
  switch (key) {
    case 'userId':
      return 'userName'
    default:
      return key
  }
}
