import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'
import { PLUGIN_SUBSCRIPTION, PLUGIN_UNSUBSCRIPTION } from '../../subscription/interface/actionTypes'
import { isArray } from 'lodash/fp'

/**
 * Presence middleware factory function.
 * @method createMiddleware
 * @param  {Object} context The factory context.
 * @return {Function} A redux middleware.
 */
export default function createMiddleware (context) {
  const operations = context.container.PresenceOperations
  const type = 'websocket'

  /*
   * Redux middleware: Watch for specific actions and trigger operations
   *    when we see them.
   */
  return next => action => {
    // Presence doesn't act on any other actions.
    if (action.type === PLUGIN_SUBSCRIPTION) {
      if (isArray(action.payload.services)) {
        const subscribeToPresence = action.payload.services.some(serviceConfig => {
          return serviceConfig.service === 'presence'
        })
        if (subscribeToPresence) {
          try {
            operations.serviceSubscribe({ service: 'presence' }, type)
          } catch (error) {
            // TODO: Figure out what to do with this error. API should get it ?
            console.error('Caught error while trying to subscribe for presence: ', error)
          }
        }
      }
    } else if (action.type === PLUGIN_UNSUBSCRIPTION) {
      if (isArray(action.payload.services) && action.payload.services.indexOf('presence') !== -1) {
        try {
          operations.serviceUnsubscribe(type)
        } catch (error) {
          // TODO: Figure out what to do with this error. API should get it ?
          console.error('Caught error while trying to unsubscribe for presence: ', error)
        }
      }
    } else if (action.type === NOTIFICATION_RECEIVED && action.payload.presenceNotification) {
      // Presence doesn't act on any other actions.
      operations.presenceReceived(action)
    }
    return next(action)
  }
}
